import './requirements.css';
import { Requirements } from '../components/Requirements';

export default {
  /* ðŸ‘‡ The title prop is optional.
  * See https://storybook.js.org/docs/react/configure/overview#configure-story-loading
  * to learn how to generate automatic titles
  */
  title: 'App Test',
  component: Requirements
}

const passwordRequirements = [
    {
      text: 'Must be at least 8 characters',
      validator: val => val.length >= 8,
    },
    {
      text: 'Must contain at least one number',
      validator: val => /\d/g.test(val),
    },
    {
      text: 'Must contain at least one lower-case letter',
      validator: val => /[a-z]/g.test(val),
    },
    {
      text: 'Must contain at least one upper-case letter',
      validator: val => /[A-Z]/g.test(val),
    }
  ];

export const Basic = () => {
    return (<Requirements 
        alue={password}
        requirements={passwordRequirements}
        onValidChange={isValid => setValid(isValid)}
    />
)};



Basic.parameters = { storySource: { source: "() => {\n    return (<Requirements \n        alue={password}\n        requirements={passwordRequirements}\n        onValidChange={isValid => setValid(isValid)}\n    />\n)}" }, ...Basic.parameters };